.card {
    width: 18rem;
    height: 100%;
    text-decoration: none;
}

.card-title {
    font-size: 1.2rem;
    text-align: center;
    font-family: 'Raleway', sans-serif;
}

.card-text {
    text-align: left;
}

.card:hover {
    border-radius: 4px;
    transition: all 0.2s ease-out;
    box-shadow: 0px 10px 14px 0px rgba(0, 0, 0, 0.2)
}
